

















































































































import ReportModal from "@/components/project/ReportModal.vue";
import {DateFormat} from "@/core/constants/DateFormat";
import {ProjectManagerSerializerWithPopulate} from "@/types/ApiTypes";
import {SequenceEvent, SequenceJSON} from "@/types/SequenceJSON";
import {format} from "date-fns";
import {Component, Vue} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component({
  components: {ReportModal},
})
export default class ProjectWorkerView extends Vue {
  @Getter("permission/email") readonly email!: string;
  projectId: string = "";
  sequenceJSON: SequenceJSON = null;
  projectManagement: ProjectManagerSerializerWithPopulate = null;
  startTime: Date = new Date();
  endTime: Date = new Date();
  timer: any = null;
  displayReportModal: boolean = false;
  tableData = [];
  formData = {
    start: 0,
    end: 0,
  };

  // FIXME
  roundSelector: number = null;

  beforeMount(): void {
    this.projectId = String(this.$route.query.id);
    this.getData();
  }

  getData(): void {
    this.api.getProjectManagement(Number(this.projectId)).then((res) => {
      this.projectManagement = res;
      this.sequenceJSON = JSON.parse(res.sequence.content);
      this.formData.start = this.sequenceJSON.tasks[0].id;
      this.formData.end = this.sequenceJSON.tasks[this.sequenceJSON.tasks.length - 1].id;
    });
  }

  stopProject(): Promise<void> {
    const command = "stop";
    return this.api
      .getPALSendCommand({id: this.projectManagement.lab.device_manager.serial_num, cmd: command})
      .then(() => this.timerStop());
  }

  timerStop(): void {
    clearInterval(this.timer);
  }

  timerStart(): void {
    this.startTime = new Date();
    this.endTime = new Date();
    this.timer = setInterval(() => (this.endTime = new Date()), 1000);
  }

  get getTime(): string {
    return `00:${format((this.endTime as any) - (this.startTime as any), DateFormat.MS)}`;
  }

  async runProject(): Promise<void> {
    const tasks = this.sequenceJSON.tasks;
    this.timerStart();
    for (let taskIndex = 0; taskIndex < tasks.length; taskIndex++) {
      const events = tasks[taskIndex].events;
      tasks[taskIndex].round = 1 + (tasks[taskIndex].round ? tasks[taskIndex].round : 0);
      for (let eventIndex = 0; eventIndex < events.length; eventIndex++) {
        const event = events[eventIndex];
        event.status = "Start";
        tasks[taskIndex].status = "Start";
        tasks[taskIndex].eventName = event.name;
        event.startTime = format((new Date() as any) - (this.startTime as any), DateFormat.MS);
        await this.doEvent(event, this.projectManagement.lab.device_manager.serial_num);
        event.status = "End";
        tasks[taskIndex].status = "End";
      }
    }
  }

  async doEvent(event: SequenceEvent, sn: string): Promise<void> {
    if (event.type === "Measurement") {
      await this.doMeasurementEvent(event, sn).then();
    } else if (event.type === "Pause") {
      await this.doPauseEvent(event, sn).then();
    } else if (event.type === "Analysis") {
      await this.doAnalysisEvent().then();
    }
  }

  async doMeasurementEvent(event, sn: string): Promise<void> {
    const checkPlayback = event.playbackChannelData.length > 0;
    const checkRecord = event.recordingData.length > 0;
    if (checkRecord && checkPlayback) {
      await this.doRecordAndPlayback(event, sn).then(async () => {
        await this.delay(event.eventLength * 1000);
        this.api.getPALGetReturn({id: sn}).then((res) => {
          this.$message.error(res);
        });
      });
    } else if (checkRecord) {
      await this.doRecord(event, sn).then(async () => {
        await this.delay(event.eventLength * 1000);
        this.api.getPALGetReturn({id: sn}).then((res) => {
          this.$message.error(res);
        });
      });

      await this.delay(event.eventLength * 2000);
      await this.s3Upload(sn).catch(console.warn);
    } else {
      console.warn("no record and playback setting");
    }
  }

  s3Upload(sn: string): Promise<string> {
    return this.api.s3upload({id: sn, src: "music3.wav", dst: `s3://${this.email}/music3.wav`});
  }

  doRecord(event, sn: string): Promise<string> {
    const Inputs = event.recordingData.map((record) => {
      return {Channel: record.channelIndex, FilePath: record.name + ".wav", Title: record.name};
    });
    const params = {
      Duration: Number(event.eventLength),
      Inputs,
    };
    const command = "record;" + JSON.stringify(params);
    return this.api.getPALSendCommand({id: sn, cmd: command});
  }

  doRecordAndPlayback(event, sn: string): Promise<string> {
    const Inputs = event.recordingData.map((record, index) => {
      return {Channel: index, FilePath: record.name + ".wav", Title: record.name};
    });
    const Outputs = event.playbackChannelData.map((playback, index) => {
      return {
        Channel: index,
        FilePath: playback.sourceFileName,
        LevelAdjustment: Number(playback.adjustment),
      };
    });
    const params = {
      Duration: Number(event.eventLength),
      Inputs,
      Outputs,
    };

    const command = "record;" + JSON.stringify(params);
    return this.api.getPALSendCommand({id: sn, cmd: command});
  }

  async doPauseEvent(event, sn: string): Promise<void> {
    await this.delay(event.eventLength * 1000);
    this.api.getPALGetReturn({id: sn}).then((res) => {
      this.$message.error(res);
    });
  }

  async doAnalysisEvent(): Promise<void> {
    const mockParams = {
      Parameters: {
        "Time Weighting": "Fast",
        "Frequency Weighting": "None",
        "Downsampling Rate": "16",
      },
      Inputs: [
        {
          "Bucket Name": "pal.data",
          "Object Key": "admin@gmail.com/music3.wav",
          "Skip Time": "0",
          "Take Time": "-1",
        },
      ],
      Output: {
        "Bucket Name": "pal.data",
        "Object Key": "admin@gmail.com/LevelVersusTime.rst",
      },
    };

    this.api.doLevelTimeApi(2, mockParams).then((res) => {
      this.$message.error(res);
    });
  }

  delay = (time: number) => {
    return new Promise((resolve) => {
      setTimeout(resolve, time);
    });
  };

  openGeneralReport(): void {
    this.displayReportModal = true;
  }
}
